.logo h1.glitch {
  color: #56c9d6;
  font-family: Robodron;
  font-weight: 800;
  position: relative;
  font-size: 3.9vw;
  padding: 60px;
}
.logo:hover > h1.glitch::before, .logo:hover > h1.glitch::after {
  padding: 3.9vw;
  color: #018594;
  content: "PARADOX";
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  overflow: hidden;
  top: 0;
}
.logo:hover > h1.glitch::before {
  left: 3px;
  text-shadow: -2px 0 red;
  animation-name: glitch-animation-1;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: reverse-alternate;
}
.logo:hover > h1.glitch::after {
  left: -3px;
  text-shadow: -2px 0 blue;
  animation-name: glitch-animation-2;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: reverse-alternate;
}

